import React from 'react'
import { connect } from 'react-redux'
import Loadable from 'react-loadable'
import {Responsive,Divider, Container, Button, Form, Grid, Header,  Segment,Icon } from 'semantic-ui-react'
import { login } from '../../actions/auth'

const SignInForm = Loadable({
    loader: () => import('./SignInForm'),
    loading: () => null,
})

const GoogleAuth = Loadable({
    loader: () => import('../../components/GoogleAuth'),
    loading: () => null,
})


class WrapperSignIn extends React.Component {

    onSubmit = formValues => {
        this.props.login(formValues)
    }

    render() {
        const Wrapper = ({SegOne,SegTwo}) => {
    
            return (
                <div>
                    <Responsive minWidth={Responsive.onlyTablet.minWidth}>
                        <Grid style={{width:700,margin:'auto'}} centered columns={2} divided padded>
                            <Grid.Column>
                                <SegOne/>
                            </Grid.Column>
                            
                            <Grid.Column>
                                <SegTwo/>
                            </Grid.Column>
                        </Grid>      
                    </Responsive>
                    <Responsive maxWidth={Responsive.onlyMobile.maxWidth}>
                        <SegOne/>
                        <Divider/>
                        <SegTwo/>
                    </Responsive>
                </div>
            )
        }
        const SegOne = () => {
            return (
                <Segment basic>
                    <div className='login-form'>
                        <Grid textAlign='center' style={{ height: '100%' }} verticalAlign='middle'>
                            <Grid.Column style={{ maxWidth: 450 }}>
                            <SignInForm onSubmit={this.onSubmit} />
                            </Grid.Column>
                        </Grid>
                    </div>
                </Segment>
            )
        }

        const SegTwo = () => {
            return (
                <Segment basic>
                    <Grid textAlign='center' style={{ height: '100%' }} verticalAlign='middle'>
                        <Grid.Column style={{ maxWidth: 450 }}>                        
                            <Header size='tiny'>Alternatively, you can:</Header>
                            <GoogleAuth/>
                            <Button style={{background:'#3b5998',boxShadow: 'rgba(0, 118, 255, 0.23) 0px 6px 20px'}} color='red' fluid size='large'>
                                <Icon name='facebook' />
                                Facebook
                                </Button>
                        </Grid.Column>
                    </Grid>
                </Segment>
            )
        }
        return <Wrapper SegOne={SegOne} SegTwo={SegTwo}/>
    }
}

const WrapperSignInForm = connect(null,{login})( WrapperSignIn )
export default WrapperSignInForm;